body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}body{background-color:#e4e4e4;color:#232526;font-family:"poppins"}
*{margin:0;padding:0;box-sizing:border-box}body{background-color:#e4e4e4;color:#232526;font-family:"poppins"}.nav{display:flex;flex-direction:column;position:fixed;width:350px;height:100vh;background-color:#88cce1;padding:10px}@media only screen and (max-width: 425px){.nav{display:none}}.nav__logo{display:flex;justify-content:center;height:250px}
*{margin:0;padding:0;box-sizing:border-box}body{background-color:#e4e4e4;color:#232526;font-family:"poppins"}.search-box{margin:50px 0 30px 0}.search-box__input{border:none;border-bottom:2px solid #232526;outline:none;width:100%;background:transparent;color:#f7f7f7;letter-spacing:.5px;font-weight:bold;font-size:18px;text-transform:uppercase;letter-spacing:.2px;text-align:left}.search-box__input::-webkit-input-placeholder{color:#f7f7f7;letter-spacing:.5px;font-weight:bold}.search-box__input:-ms-input-placeholder{color:#f7f7f7;letter-spacing:.5px;font-weight:bold}.search-box__input::placeholder{color:#f7f7f7;letter-spacing:.5px;font-weight:bold}
*{margin:0;padding:0;box-sizing:border-box}body{background-color:#e4e4e4;color:#232526;font-family:"poppins"}.filters-list__title{display:block;text-align:center;letter-spacing:.2px;padding-bottom:8px;text-transform:uppercase}
*{margin:0;padding:0;box-sizing:border-box}body{background-color:#e4e4e4;color:#232526;font-family:"poppins"}.filter-item{margin:8px 0;display:flex;justify-content:space-between;font-weight:bold}
*{margin:0;padding:0;box-sizing:border-box}body{background-color:#e4e4e4;color:#232526;font-family:"poppins"}.card-list{max-width:100%;min-height:100%;display:grid;grid-template-columns:repeat(auto-fill, 275px);grid-gap:25px;justify-content:space-around;margin-left:355px}@media only screen and (max-width: 425px){.card-list{margin:0}}
*{margin:0;padding:0;box-sizing:border-box}body{background-color:#e4e4e4;color:#232526;font-family:"poppins"}.card{padding:20px;text-align:center;background-color:#f7f7f7;border-radius:5px;box-shadow:rgba(149,157,165,.2) 0px 8px 24px;transition:.3s}.card__img{height:250px;display:block;margin:auto}.card__name{font-size:24px;margin:8px auto}.card__abv{font-size:18px;font-weight:bold;display:inline-block;margin-bottom:20px;letter-spacing:.5px}.card:hover{background-color:#88cce1;color:#f7f7f7;cursor:pointer}
